import React from 'react';
import './TopMenu.css';
import ReactDOMServer from 'react-dom/server';
import OptionText from './OptionText';

function OpenOption(e) {
  e.preventDefault();
  console.log(e.target.dataset.link);
  document.getElementById("SideMenuInner").innerHTML=ReactDOMServer.renderToString(<OptionText />);

}

function TopMenu() {
  return (
    <div className="TopMenuDiv">
      <div className="Logo">
          zedicus
      </div>
      <div className="ButtonsDiv">
          <button id="ButtText" data-link="OptionText" onClick={OpenOption}>text</button>
          <button id="ButtGradient">gradient</button>
          <button id="ButtBorder">border</button>
          <button id="ButtShadow">shadow</button>
      </div>
    </div>
  );
}

export default TopMenu;
